import { useEffect, useState } from "react";
import {useLocation, useNavigate, useParams, Link} from 'react-router-dom';


//dependencias
import * as VideoServer from "../../services/videoServer";
import * as SerieServer from "../../services/serie";
import * as TemporadaServer from "../../services/temporada";
import VideoLoadingComponent from "./videoLoading";


import * as HistorialUserServer from "../../services/historialUser";
import * as commentaryServer from "../../services/commentary";
import { useModal } from "../../hooks/useModal";

//MaterialUI
import { Container,Button,Box,Stack,Typography } from '@mui/material';
import StarIcon from "@mui/icons-material/Star";
import { makeStyles } from "@material-ui/core/styles";
import StarBorderIcon from "@mui/icons-material/StarBorder";
//components
import "../../styles/styles.css";
import ModalComentario from "./comentarioModal";
import ModalComentarios from "./listComentariosModal";
import IframeVideo from "./iframeVideo";
import { ErrorBoundary } from "react-error-boundary";
import Swal from "sweetalert2";
import capitalizarNombre from "../functions/capitalizarNombre";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(0),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
}));
/**
 * Función para mostrar el detalle de un video y guardar la puntuación.
 * @returns Componente que muestra el detalle de un video junto con el iframe
 */
const VideoDetail = () => {
  
  const history = useNavigate();
  const [show, handleShow, handleClose] = useModal(false);
  const [show2, handleShow2, handleClose2] = useModal(false);
  const [histUsers, setHistUsers] = useState();  

  const location = useLocation();
  const { id } = useParams();
  const [video, setVideo] = useState([]);
  const [serie, setSerie] = useState([]);
  const [temporada, setTemporada] = useState([]);
  const [idSerie, setIdSerie] = useState([]);
  const [idTemporada, setIdTemporada] = useState([]);

  const [loading, setLoading] = useState(true);
  const [duracion, setDuracion] = useState([]);
  const [uploadDate, setUploadDate] = useState(null);
  const [histUser, setHistUser] = useState(location.state);
  const [commentaries, setCommentaries] = useState([]);
  const [activeStar, setActiveStar] = useState(-1);
  const totalStars = 5;

  // superuser
  const data = localStorage.getItem("user");
  const user = JSON.parse(data);

  //
  const [appState, setAppState] = useState({
    loading: true
  });
  const VideoLoading = VideoLoadingComponent(video);
  
  /**
   * Función para almacenar la duración del video en un formato adecuado para mostrarla en pantalla.
   * @param {time} duration 
   * @returns Duración en el formato adecuado.
   */
  const changeDuration = (duration) => {
    let tiempo = duration.split(':');
    let new_duration = "";
    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
      /* if (tiempo[2] !=="00") {
        new_duration = " "+new_duration+" "+tiempo[2]+"s";
      } */
    }
    return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
  };
  const entrarSerie = (serieId)=>{
    history(`/seeSerie/${serieId}`,{state:histUsers});
    setIdSerie(serieId)
    console.log(serieId);
  }
  const entrarTemporada = (temporadaId)=>{
    history(`/seeTemporada/${temporadaId}`,{state:histUsers});
    setIdTemporada(temporadaId)
    console.log(temporadaId);
  }
  const handleDelete = async (videoID) => {

    Swal.fire({
      title: "¿Seguro quieres eliminar este video?",
      text: "No podras recuperar la información",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Eliminar video"
    }).then( async (result) => {
      if (result.isConfirmed) {
        await VideoServer.DeleteVideo(videoID)
        history("/");
        Swal.fire({
          title: "Video eliminado con exito",
          text: "",
          icon: "success"
        });
      }
    });
  };
  
  const getSerieAndTemporada = async (serieId,temporadaId) =>{

      const responseSerie = await SerieServer.getSerie(serieId)
      console.log(responseSerie);
      setSerie(responseSerie)

      const responseTemporada = await TemporadaServer.getTemporada(temporadaId)
      console.log(responseTemporada);
      setTemporada(responseTemporada)
  }
  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorial();
      /* console.log(res); */
      if (res && user.id) {
        setActiveStar(res.user_score-1);
        setHistUsers(res);
        setLoading(false);
  
        // Suponiendo que `res` tiene la estructura { user_score: ... }
        if (res.user_score !== undefined) {
          setActiveStar(res.user_score - 1);
        }
      } else {
        history("/loginuser");
      }
    } catch (error) {
      console.log(error);
    }
  };
  

  
  /**
   * Función para obtener la información del video en reproducción
   */
  useEffect(() => {
    getHistorialUsers();
    
    const getVideo = async (videoID) => {
      const res = await VideoServer.getVideo(videoID);
      const video = res;
      //console.log(video);

      setUploadDate(new Date(res.upload_date).toDateString());
      setDuracion(changeDuration(res.duration));
      setVideo({
        ...video,
        duration: duracion,           
      });
      setAppState({ loading: false});
      
    };

    getVideo(id);
    /*  */
    const getCommentaries = async (videoId) => {
      const res = await commentaryServer.ListCommentaryVideo({'video_id':videoId});
      setCommentaries(res);
    }
    getCommentaries(id);
    /*  */
    getSerieAndTemporada(video.serie,video.temporada)
  }, [duracion, id, setVideo, video.title_espanol, video.url_esp, video.url_vimeo_esp,video.serie_id]);
  

   
  /**
   * Función para cambiar la calificación del video.
   * @param {int} index 
   */
  const handleClick = async (index) => {
    setActiveStar(index);
    let cumulative_score = video.cumulative_score;
    let number_votes = video.numberOfVotes;

    if (histUser.user_score) {
      cumulative_score = cumulative_score - histUser.user_score + index+1;
      const score_video = cumulative_score/number_votes;
      setHistUser({...histUser, user_score:index+1});
      await VideoServer.partialUpdateVideo(video.id, {'cumulative_score': cumulative_score, 'score': score_video})

    } else {
      number_votes +=1;
      cumulative_score = cumulative_score + index+1;
      const score_video = cumulative_score/number_votes;
      setHistUser({...histUser, user_score:index+1});
      await VideoServer.partialUpdateVideo(video.id, {'cumulative_score': cumulative_score,'numberOfVotes': number_votes,'score': score_video})
    }
    await HistorialUserServer.updateHistorialUser(histUser.id,{'user_score': index+1});
 
  };
  
  
  const classes = useStyles();

  if (!loading ) {
    return (
    <Container>
      {appState.loading  ?  <VideoLoading isLoading={appState.loading} video={video} /> :""}
      <div>
        <div className="d-flex justify-content-end">
          <button  onClick={() => window.history.back()} className="btn btn-light mt-4 my-2" value="Volver">Volver</button>
        </div>
      </div>
      <div className={classes.paper}>
        <h3>
          {video.title_espanol}
        </h3>
      </div>
      <br></br>
      <div className="row">
        <div className="col-8 iframe1">
          <IframeVideo
            video={video}
            >
          </IframeVideo>
        </div>
        <div className="col infoVideo" >
          <Typography component="h5" variant="h6" style={{color:"white",borderBottom:"3px solid white",paddingBottom:'5px'}} align="center">
            {video.duration}
          </Typography>
          <br></br>
          {user.is_superuser &&
            <div className="d-flex justify-content-between mb-2">
              
              <button
                onClick={() => history(`/updateVideo/${video.id}`)}
                className="btn  btn-outline-primary form-control mx-1"
                
                >
                Actualizar
              </button>
              
              <button
                onClick={() => video.id && handleDelete(video.id)}
                className=" btn  btn-outline-danger form-control mx-1"
                >
                Eliminar
              </button>
            </div>
          }

          <div className="d-flex justify-content-center flex-column align-items-center">
          <Typography component="h5" variant="h6">
            Ayúdanos a calificar   &nbsp;
            <Box 
              sx={{
                display: "inline-flex",
                position: "relative",
                cursor: "pointer",
                textAlign: "left",
              }}
            >
              {[...new Array(totalStars)].map((arr, index) => {
                return (
                  <Box 
                  key={index} 
                  position="relative"
                  sx={{
                    cursor: "pointer",
                  }}
                  onClick={(e) => handleClick(index)}
                  >
                    <Box 
                      sx={{
                        width: index <= activeStar ? "100%" : "0%",
                        overflow: "hidden",
                        position: "absolute",
                      }}
                    >
                      <StarIcon />
                    </Box>
                    <Box>
                      <StarBorderIcon />
                    </Box>
                  </Box>
                )
              })}
            </Box>
          </Typography>
          <br></br>
          <div className="d-flex ">

            <Stack  alignItems="center" className="p-2">
              <Button 
              variant="contained" 
              color="success"
              type="submit"
              onClick={handleShow}>
                Realizar comentario
              </Button>
            </Stack>
            {commentaries ? 
              <Stack  alignItems="center" className="p-2">
                <Button 
                  variant="contained" 
                  color="primary"
                  type="submit"
                  onClick={handleShow2}>
                  Ver otros comentarios
                </Button>
              </Stack>
              : 
              null
            }
          </div>
            
          <div className="mt-3">

            <div className="table-responsive ">
              <table className="table  text-light ">
                <thead>
                  <tr className="">
                    <td scope="">Autores</td>
                    <td>
                    {video.autor && (
                      video.autor.map(autor => (
                        <li key={autor.id}><small className="">{capitalizarNombre(autor.autor)}</small></li>
                      ))
                    )}
                    </td>
                  </tr>
                  {video.tipe_of_video === 2 && (
                    <>
                      <tr>
                        <th scope="col">Serie</th>
                        <th scope="col">
                          <a 
                            className="text-primary" 
                            style={{ cursor: "pointer" }} 
                            onClick={() => entrarSerie(serie.id)}
                          >
                            {serie.serie}
                          </a>
                        </th>
                      </tr>
                      <tr>
                        <th scope="col">Temporada</th>
                        <th scope="col">
                          <a 
                            className="text-primary" 
                            style={{ cursor: "pointer" }} 
                            onClick={() => entrarTemporada(temporada.id)}
                          >
                            {temporada.temporada}
                          </a>
                        </th>
                      </tr>
                    </>
                  )}


                </thead>
              </table>
            </div>
            
            
            
          </div>
        </div>
        </div>
        <div>
            <h5>Descripción</h5>
          <p className="mt-3">
              {video.description_esp}
          </p>
        </div>
        <br/>
        <div className="table-responsive ">
          <table className="table  text-light ">
            <thead>
              <tr>
                <th scope="col">Categorías</th>
                <th scope="col">Especialidades</th>
                <th scope="col">Subespecialidades</th>
                {/* <th scope="col">Autores</th> */}
                <th scope="col">Palabras claves</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                {video.categorias && (
                  video.categorias.map(categoria => (
                    <li key={categoria.id}><small className="">{categoria.categoria}</small></li>
                  ))
                )}
                </td>
                <td>
                  {video.especialidad && (
                    video.especialidad.map(especialidad => (
                      <li key={especialidad.id}><small className="">{especialidad.especialidad}</small></li>
                    ))
                  )}
                </td>
                <td>
                {video.subEspecialidad && (
                  video.subEspecialidad.map(subEspecialidad => (
                    <li key={subEspecialidad.id}><small className="">{subEspecialidad.subEspecialidad}</small></li>
                  ))
                )}
                </td>
                {/* <td>
                {video.autor && (
                  video.autor.map(autor => (
                    <li key={autor.id}><small className="">{autor.autor}</small></li>
                  ))
                )}
                </td> */}
                <td>
                  {video.palabraClave && (
                      video.palabraClave.map(palabraClave => (
                        <li key={palabraClave.id}><small className="">{palabraClave.palabraClave}</small></li>
                      ))
                  )}
                </td>
              </tr>
              
            </tbody>
          </table>
        </div>
        
      </div>
      
      <ModalComentario
        handleClose={handleClose}
        show={show}
        histUser={histUser}
        videoID ={id}
      ></ModalComentario>
      <ModalComentarios
        handleClose={handleClose2}
        show={show2}
        histUser={histUser}
        commentaries = {commentaries}
        videoID= {id}
      ></ModalComentarios>
    </Container>
    );
  }
  
  
};

export default VideoDetail;
